}
.banner .wrapper-dropdown.active .dropdown {
  opacity: 1;
  pointer-events: auto;
}

.transport {
  background: url("../img/transport-back.png") no-repeat center;
  background-size: cover;
  max-width: 1920px;
  margin: 0 auto;
  padding-top: 101px;
  padding-bottom: 70px;
  position: relative;
}
@media (max-width: 900px) {
  .transport {
    padding-top: 65px;
  }
}
.transport::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background: rgba(6, 15, 33, 0.5);
  z-index: 1;
}
@media (max-width: 900px) {
  .transport::before {
    background: rgba(0, 0, 0, 0.6);
  }
}
.transport__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 28px;
  grid-row-gap: 60px;
  z-index: 2;
  position: relative;
}
@media (max-width: 900px) {
  .transport__container {
    grid-template-columns: 1fr;
    grid-row-gap: 12px;
  }
}
.transport h2 {
  font-size: 40px;
  line-height: 135%;
  font-weight: 400;
  color: #fff;
  margin: 0;
}
@media (max-width: 900px) {
  .transport h2 {
    font-size: 30px;
    margin-bottom: 16px;
  }
}
.transport p {
  font-size: 18px;
  line-height: 145%;
  font-weight: 400;
  color: #fff;
  display: block;
  margin: 0;
  padding-top: 26px;
  padding-left: 20px;
  padding-right: 10px;
}
@media (max-width: 900px) {
  .transport p {
    grid-row: 4;
    font-size: 14px;
    padding-top: 0;
    padding-left: 0;
  }
}
.transport p a {
  color: #fff;
  text-decoration: underline;
}
.transport__card {
  text-decoration: none;
  padding: 30px 30px 252px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.transport__card:hover {
  transition: 0.2s;
  opacity: 0.7;
}
@media (max-width: 900px) {
  .transport__card {
    padding: 24px 24px 115px;
    border-radius: 4px;
  }
  .transport__card:hover {
    transition: 0.2s;
    opacity: 1;
  }
}
@media (max-width: 900px) {
  .transport__card:first-child {
    margin-top: 18px;
  }
}
@media (max-width: 900px) {
  .transport__card:last-child {
    margin-bottom: 18px;
  }
}
.transport__card--pink {
  background: #FFE7EF url("../img/transport-2.png") no-repeat bottom center;
}
@media (max-width: 1240px) {
  .transport__card--pink {
    background: #FFE7EF url("../img/transport-2.png") no-repeat 0% -20%;
  }
}
@media (max-width: 900px) {
  .transport__card--pink {
    background: #FFE7EF url("../img/transport-2.png") no-repeat bottom right;
    background-size: 345px 166px;
  }
}
.transport__card--pink span {
  background: #F4C5D4;
}
.transport__card--green {
  background: #EFF6E8 url("../img/transport-1.png") no-repeat bottom left;
}
@media (max-width: 900px) {
  .transport__card--green {
    background-size: 236px 81px;
  }
}
.transport__card--green span {
  background: #D7EDC3;
}
.transport__card-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.transport__card-top b {
  font-size: 28px;
  line-height: 145%;
  font-weight: 700;
  color: #373737;
  padding-left: 6px;
}
@media (max-width: 900px) {
  .transport__card-top b {
    font-size: 18px;
  }
}
.transport__card-top span {
  display: block;
  border-radius: 23px;
  padding: 11px;
  font-size: 18px;
  line-height: 145%;
  font-weight: 700;
  color: #373737;
}
@media (max-width: 900px) {
  .transport__card-top span {
    font-size: 12px;
    padding: 8px;
  }
}

.transport-popup {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 22;
}
.transport-popup--green .transport-popup__container {
  background: #EFF6E8;
}
@media (max-width: 1023px) {
  .transport-popup--green img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}
.transport-popup--pink .transport-popup__container {
  background: #FFE7EF;
}
.transport-popup--pink .transport-popup__container img {
  margin-right: -114px;
}

.transport-popup__container {
