  background: transparent url("../img/popup-close.svg") no-repeat center;
  width: 30px;
  height: 30px;
  display: block;
  position: absolute;
  top: 0;
  right: -46px;
}
@media (max-width: 1023px) {
  .transport-popup__container button {
    display: flex;
    top: 33px;
    right: 11px;
    width: 78px;
    padding: 0;
    justify-content: flex-start;
    background: transparent url("../img/close-mob.svg") no-repeat 100% 6px;
  }
